import style from './style.module.css';



const MobileStyledText = (props) =>{

    return (
        <div className = {style.wrapper}>
            <h2 className = {style.leftLayer}>{props.text}</h2>
            <h2 className = {style.middleLayer}>{props.text}</h2>
            <h2 className = {style.rigthLayer}>{props.text}</h2>
        </div>
    );
}


export default MobileStyledText;