import style from './style.module.css';
import { useMediaQuery } from 'react-responsive'
import SmallStyledText from '../SmallStyledText/SmallStyledText';
import MobileStyledText from '../MobileStyledText/MobileStyledText';


const Tickets = (props) =>{

    const isDesktopOrLaptop = useMediaQuery({ minDeviceWidth: 1224 });
    if (isDesktopOrLaptop === true){
    return (
        <div>
            <div className = {style.desktopWrapper}>
                        <div className = {style.customLabelDesktop}>
                            <SmallStyledText text = {'ПОКУПКА БИЛЕТОВ'}/>
                        </div>
                        <div className = {style.buttonWrapper}>
                            <button className = {style.ticketButton}>
                                КУПИТЬ БИЛЕТ
                            </button>
                        </div>
                            <div className = {style.descriptionWrapper}>
                                <h2 className = {style.description}>Мы не публикуем подробное расписание всех 
                                активностей на нашем мероприятии, чем подтверждаем название ярмарки.
                                    Нашим людям нравится, когда результат превышает ожидания,
                                    партия билетов в потоке продаётся моментально.
                                        На ивент 22-23 августа 400 билетов раскупили за 7 минут
                                </h2>
                            </div>
                    </div>
        </div>
    );
    }else{
        return(
            <div>
            <div className = {style.mobileWrapper}>
                        <div className = {style.customLabelMobile}>
                            <MobileStyledText text = {'ПОКУПКА БИЛЕТОВ'}/>
                        </div>
                        <div className = {style.buttonWrapper}>
                            <button className = {style.ticketButton}>
                                КУПИТЬ БИЛЕТ
                            </button>
                        </div>
                            <div className = {style.descriptionWrapperMobile}>
                                <h2 className = {style.descriptionMobile}>Мы не публикуем подробное расписание всех 
                                активностей на нашем мероприятии, чем подтверждаем название ярмарки.
                                    Нашим людям нравится, когда результат превышает ожидания,
                                    партия билетов в потоке продаётся моментально.
                                        На ивент 22-23 августа 400 билетов раскупили за 7 минут
                                </h2>
                            </div>
                    </div>
        </div>
        );
    }
}

export default Tickets;