import style from './style.module.css';
import SmallStyledText from '../SmallStyledText/SmallStyledText';
import MobileStyledText from '../MobileStyledText/MobileStyledText';
import { useMediaQuery } from 'react-responsive'



const Description = (props) =>{

    const isDesktopOrLaptop = useMediaQuery({ minDeviceWidth: 1224 });
    if (isDesktopOrLaptop === true){
    return (
        <div>
            <div className = {style.desktopWrapper}>
                <div className = {style.firstItem}>
                        <div className = {style.customLabelDesktop}>
                            <SmallStyledText text = {'О ПРОЕКТЕ'}/>
                        </div>
                        <h2 className = {style.description}>
                            Свободная от ярлыков ГИК-ярмарка проводится
                        с целью объедиения любителей фильмов, сериалов, комиксов, аниме, игр, k-pop, книг и так далее и 
                        их дальнейшего знакомства и живого общения в век информационных технологий.
                        </h2>
                </div>
                <div className = {style.secondItem}>
                <div className = {style.customLabelDesktop}>
                            <SmallStyledText text = {'НАШИ ЗАДАЧИ'}/>
                        </div>
                        <h2 className = {style.description}>
                            Распространение свободного творчества, самовыражения и популяризация
                            искусства среди населения. Для нас наивысшая ценность - человек, его свободы,
                            а значит свобода самовыражения, слова и творчества, не ограниченные рамками и
                            стандартами.
                        </h2>
                    </div>
                    <div className = {style.thirdItem}>
                      
                        <div className = {style.customLabelDesktop}>
                            <SmallStyledText text = {'НАША ПОЗИЦИЯ'}/>
                        </div>
                        <h2 className = {style.description}>
                            Делать душевно от людей и для людей, таких же,
                            как и мы: страстно увлечённых творчеством и своим хобби.
                        </h2>
                    </div>
            </div>
        </div>
    );
    }else{
        return(
            <div>
          <div className = {style.mobileWrapper}>
                <div className = {style.firstItem}>
                        <div className = {style.customLabelMobile}>
                            <MobileStyledText text = {'О ПРОЕКТЕ'}/>
                        </div>
                        <h2 className = {style.descriptionMobile}>
                            Свободная от ярлыков ГИК-ярмарка проводится
                        с целью объедиения любителей фильмов, сериалов, комиксов, аниме, игр, k-pop, книг и так далее и 
                        их дальнейшего знакомства и живого общения в век информационных технологий.
                        </h2>
                        </div>
                        <div className = {style.secondItem}>
                            <div className = {style.customLabelMobile}>
                                <MobileStyledText text = {'НАШИ ЗАДАЧИ'}/>
                            </div>
                            <h2 className = {style.descriptionMobile}>
                                Распространение свободного творчества, самовыражения и популяризация
                                искусства среди населения. Для нас наивысшая ценность - человек, его свободы,
                                а значит свобода самовыражения, слова и творчества, не ограниченные рамками и
                                стандартами.
                            </h2>
                        </div>
                        <div className = {style.thirdItem}>
                            <div className = {style.customLabelMobile}>
                                <MobileStyledText text = {'НАША ПОЗИЦИЯ'}/>
                            </div>
                            <h2 className = {style.descriptionMobile}>
                                Делать душевно от людей и для людей, таких же,
                                как и мы: страстно увлечённых творчеством и своим хобби.
                            </h2>
                        </div>
                    </div>
            </div>
        );
    }
}

export default Description;