import style from './style.module.css';
import TikTok from './TikTokLogo.png';
import Vk from './VkLogo.png';
import Instagram from './InstagramLogo.png';
import { useMediaQuery } from 'react-responsive';

const Footer = (props) =>{

    const isDesktopOrLaptop = useMediaQuery({ minDeviceWidth: 1224 });

    if (isDesktopOrLaptop === true){
    return (
        <div className = {style.desktopWrapper}>
            <div className = {style.labelTextD}>
                    <h2>Наши аккаунты в социальных сетях</h2>
                </div>
                <div className = {style.wrapperD}>
                    <div className = {style.footerIconTikTok}>
                        <a href="https://www.tiktok.com/@freefandomparty">
                            <img className = {style.footerIconD} alt = "socialMedia" src = {TikTok}/>
                        </a>
                    </div>
                    <div className = {style.footerIconVk}>
                        <a href="https://vk.com/ffpmsk">
                            <img className = {style.footerIconD} alt = "socialMedia" src = {Vk}/>
                        </a>
                    </div>
                    <div className = {style.footerIconInstagram}>
                        <a href="https://www.instagram.com/@freefandomparty">
                            <img className = {style.footerIconD} alt = "socialMedia" src = {Instagram}/>
                        </a>
                    </div>
                </div>
                <h2 className = {style.copyrightLabel}>©©© 2015-2021 Triple F ©©©</h2>
            </div>
    );
    }else{
        return (
            <div className = {style.mobileWrapper}>
                <div className = {style.labelText}>
                        <h2>Наши аккаунты в социальных сетях</h2>
                    </div>
                    <div className = {style.wrapper}>
                        <div className = {style.footerIconTikTok}>
                            <a href="https://www.tiktok.com/@freefandomparty">
                                <img className = {style.footerIcon} alt = "socialMedia" src = {TikTok}/>
                            </a>
                        </div>
                        <div className = {style.footerIconVk}>
                            <a href="https://vk.com/ffpmsk">
                                <img className = {style.footerIcon} alt = "socialMedia" src = {Vk}/>
                            </a>
                        </div>
                        <div className = {style.footerIconInstagram}>
                            <a href="https://www.instagram.com/@freefandomparty">
                                <img className = {style.footerIcon} alt = "socialMedia" src = {Instagram}/>
                            </a>
                        </div>
                    </div>
                    <h2 className = {style.copyrightLabel}>©©© 2015-2021 Triple F ©©©</h2>
                </div>
        );
    }
}

export default Footer;