import style from './style.module.css';
import { useMediaQuery } from 'react-responsive';
import Gallery1 from './Gallery1.jpg';
import Gallery2 from './Gallery2.jpg';
import Gallery3 from './Gallery3.jpg';
import Gallery4 from './Gallery4.jpg';
import Gallery5 from './Gallery5.jpg';
import Gallery6 from './Gallery6.jpg';
import Gallery7 from './Gallery7.jpg';


const Greetings = (props) =>{

    const isDesktopOrLaptop = useMediaQuery({ minDeviceWidth: 1224 });
    if (isDesktopOrLaptop === true){
    return (
        <div>
            <div className = {style.desktopWrapper}>
                <div className = {style.aboutItemLabelDesktop}><h2 className = {style.aboutLabelDesktop}>Галлерея</h2></div>
                <div className = {style.aboutItemImg}><img src = {Gallery1} className = {style.galleryImgDesktop} alt = 'gallery item'/></div>
                <div className = {style.aboutItemLabelDesktop}><h2 className = {style.aboutLabelDesktop}>Все это - Triple F</h2></div>
                <div className = {style.aboutItemImg}><img src = {Gallery2} className = {style.galleryImgDesktop} alt = 'gallery item'/></div>
                <div className = {style.aboutItemImg}><img src = {Gallery3} className = {style.galleryImgDesktop} alt = 'gallery item'/></div>
                <div className = {style.aboutItemLabelDesktop}><h2 className = {style.aboutLabelDesktop}></h2></div>
                <div className = {style.aboutItemImg}><img src = {Gallery4} className = {style.galleryImgDesktop} alt = 'gallery item'/></div>
                <div className = {style.aboutItemImg}><img src = {Gallery5} className = {style.galleryImgDesktop} alt = 'gallery item'/></div>
                <div className = {style.aboutItemLabelDesktop}><h2 className = {style.aboutLabelDesktop}>И это тоже!</h2></div>
                <div className = {style.aboutItemImg}><img src = {Gallery6} className = {style.galleryImgDesktop} alt = 'gallery item'/></div>
                <div className = {style.aboutItemImg}><img src = {Gallery7} className = {style.galleryImgDesktop} alt = 'gallery item'/></div>
                <div className = {style.aboutItemLabelDesktop}><h2 className = {style.aboutLabelDesktop}></h2></div>
            </div>
        </div>
    );
    }else{
        return(
        <div>
            <div className = {style.mobileWrapper}>
                <div className = {style.aboutItemLabel}><h2 className = {style.aboutLabel}>Галлерея</h2></div>
                <div className = {style.aboutItem}><img src = {Gallery1} className = {style.galleryImg} alt = 'gallery item'/></div>
                <div className = {style.aboutItem}><img src = {Gallery2} className = {style.galleryImg} alt = 'gallery item'/></div>
                <div className = {style.aboutItemLabel}><h2 className = {style.aboutLabel}>Все это - Triple F</h2></div>
                <div className = {style.aboutItem}><img src = {Gallery3} className = {style.galleryImg} alt = 'gallery item'/></div>
                <div className = {style.aboutItem}><img src = {Gallery4} className = {style.galleryImg} alt = 'gallery item'/></div>
                <div className = {style.aboutItemLabel}><h2 className = {style.aboutLabel}>И это тоже!</h2></div>
                <div className = {style.aboutItem}><img src = {Gallery5} className = {style.galleryImg} alt = 'gallery item'/></div>
                <div className = {style.aboutItem}><img src = {Gallery6} className = {style.galleryImg} alt = 'gallery item'/></div>
                <div className = {style.aboutItem}><img src = {Gallery7} className = {style.galleryImg} alt = 'gallery item'/></div>
            </div>
        </div>
        );
    }
}

export default Greetings;