import React from 'react';
import style from './style.module.css';
import Location from '../Location/Location';
import { useMediaQuery } from 'react-responsive'


const QuickLocation = () =>{


  const isDesktopOrLaptop = useMediaQuery({ minDeviceWidth: 1224 });
  if (isDesktopOrLaptop === true){
    return(
        <div className = {style.desktopWrapper}>
              <div className = {style.infoTipDesktop}>
                  <p className = {style.infoTitleDesktop}>Мы здесь 🚩</p>
                  <p className = {style.infoAddressDesktop}>Москва, ул. Никулинская 2 к1</p>
                  <div className = {style.locationIcon}>
                 </div>
              </div>
              <div className = {style.location}>
                <Location/>
              </div>
        </div>
    );
  }else{
    return(
        <div className = {style.mobileWrapper}>
            <div className = {style.infoTip}>
                <p className = {style.infoTitle}>Мы здесь</p>
                <p className = {style.infoAddress}>Москва, ул. Никулинская 2 к1</p>
                <div className = {style.locationIcon}>
              </div>
            </div>
            <div className = {style.location}>
              <Location/>
            </div>
        </div>
    );
  }
}

export default QuickLocation;